export const LEGACY_CREATE_PLAN_UI_SLUG = 'legacy';

// Create plan slugs
export const CREATE_FREE_SLUG = '2023a-free';
export const CREATE_PRO_SLUG = '2023b-business';
export const CREATE_ENTERPRISE_SLUG = '2022b-business';

/*
 * We are using the ID and base price for "pro-v4" so that the
 * UpgradeModal (for Zuora plans) can use them.
 * This is because UpgradeModal only lets you upgrade to a Pro plan
 * And we need to provide the ID and price for the Pro plan.
 * We could get it from the available list of plans, but that involves
 * a lot of prop drilling in a lot of places.
 * It's easier to just hardcode it here.
 * This code should be removed once we've migrated to Orb.
 */
export const PRO_V4_ID = '5ed6c554f0f6d000bb4dd075';
export const ORB_PRO_ID = '5578334277324e6959523542';
export const PRO_V4_PRICE = 19;

export type AccountPlanData = {
  on_pro_trial: boolean;
  type_name: string;
  type_slug: string;
};

export const getStandardPlanNames = () => [
  FREE_NAME,
  STARTER_NAME,
  PRO_NAME,
  BUSINESS_NAME,
  ENTERPRISE_NAME,
  LEGACY_CREATE_PLAN_UI_NAME,
];

export const isPlanIndia = (planSlug: string = ''): boolean => {
  return [
    ORB_INDIA_STARTER_SLUG,
    ORB_INDIA_PRO_SLUG,
    ORB_INDIA_BUSINESS_SLUG,
  ].includes(planSlug);
};

export const isPlanFree = (planSlug: string = ''): boolean =>
  planSlug === FREE_SLUG;

export const isPlanStarter = (planSlug: string = ''): boolean => {
  return [
    STARTER_SLUG,
    ORB_STARTER_SLUG,
    LEGACY_MULTIPLAYER_STARTER_SLUG,
  ].includes(planSlug);
};

export const isPlanFreeOrStarter = (planSlug: string = ''): boolean =>
  isPlanFree(planSlug) || isPlanStarter(planSlug);

export const isPlanPro = (planSlug: string = ''): boolean => {
  return [PRO_SLUG, ORB_PRO_SLUG, ORB_INDIA_PRO_SLUG].includes(planSlug);
};

export const isPlanBusiness = (planSlug: string = ''): boolean => {
  return [BUSINESS_SLUG, ORB_BUSINESS_SLUG, ORB_INDIA_BUSINESS_SLUG].includes(
    planSlug
  );
};

export const isPlanEnterprise = (planSlug: string = ''): boolean =>
  planSlug === ENTERPRISE_SLUG;

export const isPlanEnterpriseTrial = (planSlug: string = ''): boolean =>
  planSlug === ENTERPRISE_TRIAL_SLUG;

export const isPlanEnterpriseGrid = (planSlug: string = ''): boolean =>
  planSlug === ENTERPRISE_GRID_SLUG;

export const isPlanEnterpriseLevel = (planSlug: string = ''): boolean =>
  isPlanEnterprise(planSlug) ||
  isPlanEnterpriseTrial(planSlug) ||
  isPlanEnterpriseGrid(planSlug);

export const isPlanOpenSource = (planSlug: string = ''): boolean =>
  planSlug === OPEN_SOURCE_SLUG;

export const isPlanOpenSourceUnlimited = (planSlug: string = ''): boolean =>
  planSlug === OPEN_SOURCE_UNLIMITED_SLUG;

export const isPlanTypeOpenSource = (planSlug: string = ''): boolean =>
  isPlanOpenSource(planSlug) || isPlanOpenSourceUnlimited(planSlug);

export const isPlanAgencyBenefit = (planSlug: string = ''): boolean =>
  planSlug === AGENCY_BENEFIT_SLUG;

export const isPlanNetlifriend = (planSlug: string = ''): boolean =>
  planSlug === NETLIFRIEND_SLUG;

export const isPlanPublicGood = (planSlug: string = ''): boolean =>
  planSlug === PUBLIC_GOOD_SLUG;

export const isPlanLegacyMultiplayerStarter = (
  planSlug: string = ''
): boolean => planSlug === LEGACY_MULTIPLAYER_STARTER_SLUG;

export const isPlanLegacyCreate = (planSlug: string = ''): boolean =>
  planSlug === LEGACY_CREATE_PLAN_UI_SLUG;

export const normalizeTeamPlanName = (planName?: string) =>
  planName?.toLowerCase().replace(PERSONAL_SLUG, STARTER_SLUG);

export const humanizePlanName = (planName?: string) => {
  return planName ? planName.split('_').map(capitalize).join(' ') : '';
};

const isPlanRankedHigher = (
  planSlug: string | undefined,
  targetRank: number
): boolean => {
  const rank = planSlug ? plansRanking[planSlug] || -1 : -1;
  return rank >= targetRank;
};

export const isPlanProOrHigher = (planSlug?: string): boolean =>
  isPlanRankedHigher(planSlug, 2);

export const isPlanBusinessOrHigher = (planSlug?: string): boolean =>
  isPlanRankedHigher(planSlug, 3);

export const isPlanEnterpriseOrHigher = (planSlug?: string): boolean =>
  isPlanRankedHigher(planSlug, 4);

export const isPlanOrb = (planSlug: string = ''): boolean => {
  return [
    ORB_STARTER_SLUG,
    ORB_PRO_SLUG,
    ORB_BUSINESS_SLUG,
    ORB_INDIA_STARTER_SLUG,
    ORB_INDIA_PRO_SLUG,
    ORB_INDIA_BUSINESS_SLUG,
    LEGACY_MULTIPLAYER_STARTER_SLUG,
  ].includes(planSlug);
};

export const isPlanSelfServe = (planSlug: string = ''): boolean => {
  return [
    FREE_SLUG,
    STARTER_SLUG,
    PRO_SLUG,
    BUSINESS_SLUG,
    ORB_STARTER_SLUG,
    ORB_PRO_SLUG,
    ORB_BUSINESS_SLUG,
    ORB_INDIA_STARTER_SLUG,
    ORB_INDIA_PRO_SLUG,
    ORB_INDIA_BUSINESS_SLUG,
    ENTERPRISE_TRIAL_SLUG,
  ].includes(planSlug);
};

export const isPlanStandard = (planSlug: string = ''): boolean => {
  return [
    FREE_SLUG,
    STARTER_SLUG,
    PRO_SLUG,
    ENTERPRISE_SLUG,
    ORB_STARTER_SLUG,
    ORB_PRO_SLUG,
    ORB_INDIA_STARTER_SLUG,
    ORB_INDIA_PRO_SLUG,
  ].includes(planSlug);
};

export const isPlanScheduledDowngrade = (planSlug: string = ''): boolean => {
  return [
    ORB_PRO_SLUG,
    ORB_INDIA_PRO_SLUG,
    ORB_BUSINESS_SLUG,
    ORB_INDIA_BUSINESS_SLUG,
  ].includes(planSlug);
};

export const isPlanValidForLockedAccounts = (
  planSlug: string = ''
): boolean => {
  return [PRO_SLUG, ORB_PRO_SLUG, ORB_INDIA_PRO_SLUG, ENTERPRISE_SLUG].includes(
    planSlug
  );
};

export const getAccountCreatePlan = (planSlug?: string): string => {
  if (!planSlug) return CREATE_FREE_SLUG;
  // We don't have the actual information so we return the expected plan:
  if (isPlanEnterpriseLevel(planSlug)) return CREATE_ENTERPRISE_SLUG;
  if (isPlanPro(planSlug) || isPlanBusiness(planSlug)) return CREATE_PRO_SLUG;
  return CREATE_FREE_SLUG;
};

export const getPlanCapabilities = (planSlug?: string): string[] => {
  if (!planSlug) return [];
  const planInfo = plansInfo.find((plan) => plan.plans.includes(planSlug));
  if (!planInfo) return [];
  return planInfo.features;
};

export const getPlanCapabilitiesForUI = (planSlug: string = ''): string[] => {
  if (!planSlug) return [];
  const planLimits = plansLimits.find((plan) => plan.plans.includes(planSlug));
  if (!planLimits) return [];
  return planLimits.limits;
};

export const getCreatePlanCapabilities = (planSlug: string = '') => {
  if (planSlug === CREATE_FREE_SLUG)
    return [
      '2 Visual Editor presets',
      'Restart delay after Visual Editor hibernation',
    ];
  if (planSlug === CREATE_PRO_SLUG)
    return [
      '100 Visual Editor presets',
      'Immediate restart after Visual Editor hibernation',
    ];
  if (planSlug === CREATE_ENTERPRISE_SLUG)
    return [
      'Unlimited Visual Editor presets',
      'Immediate restart after Visual Editor hibernation',
    ];
  return [];
};

export const getPlanChangeInfo = (planSlug?: string) => {
  if (isPlanFree(planSlug)) return plansChange[FREE_SLUG];
  if (isPlanStarter(planSlug)) return plansChange[STARTER_SLUG];
  if (isPlanPro(planSlug)) return plansChange[PRO_SLUG];
  if (isPlanEnterprise(planSlug)) return plansChange[ENTERPRISE_SLUG];
  return null;
};

export const getPlanMemberMonthlyPrice = (
  plan?:
    | {
        member_monthly_dollar_price?: string | undefined;
        monthly_seats_addon_dollar_price: string;
      }
    | undefined
): string => {
  if (!plan) return '0';
  return (
    plan.member_monthly_dollar_price ||
    plan.monthly_seats_addon_dollar_price ||
    '0'
  );
};
